import vid1 from "../assets/A thousand word speller - HD 720p.mov"
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { ScrollToPlugin } from 'gsap/ScrollToPlugin';
console.log(vid1);

// https://gsap.com/community/forums/topic/32322-clip-path-to-gsap-animation/
// https://developer.mozilla.org/en-US/docs/Web/CSS/clip-path

let scrollActing = false;
// An option to avoid using this is to simply add an event listener for hover
// When I'm hovering/focusing the item, I have the event listener for scroll
// Otherwise, I don't have it


const sidebar = document.getElementById("sidebar");
let currElement = document.querySelector("#first-element");
currElement.classList.add("active")

// <video src="assets/A thousand word speller - HD 720p.mov" muted autoplay loop></video>

const video_address = {
    "A thousand-word speller": vid1
}

function createVideo(address) {
    var videoElement = document.createElement("video");

    // Set attributes for the video element
    videoElement.src = address
    videoElement.muted = true;
    videoElement.autoplay = true;
    videoElement.loop = true;

    const videoHolder = document.querySelector(".video-background");
    var existingVideo = videoHolder.querySelector('video');

    if (existingVideo) {
        // If a video element exists, remove it
        gsap.to(existingVideo, {
            filter: 'blur(40px)',
            opacity: 0,
            duration: 0.5,
            onComplete: () => existingVideo.remove()
        })
    }
    videoHolder.appendChild(videoElement);
    gsap.from(videoElement, {
        filter: 'blur(40px)',
        opacity: 0,
        duration: 1
    });
}

function deleteVideo() {
    const videoHolder = document.querySelector(".video-background");
    var existingVideo = videoHolder.querySelector('video');

    if (existingVideo) {
        // If a video element exists, remove it
        gsap.to(existingVideo, {
            filter: 'blur(40px)',
            opacity: 0,
            duration: 0.5,
            onComplete: () => existingVideo.remove()
        })
    }
}




const contentIDs = {
    "Mynd Music": "mynd-music",
    "Waterfall": "waterfall"
}

// Instead of manually detecting the middle element
// I can also use scrolltrigger to track all elements
// When they reach the middle I animate
// This approach is also fine, I just have to make an animation that affect middleElemetn
function updateCanvasOnScroll() {
    let elements = document.elementsFromPoint(80, Math.floor(window.innerHeight / 2));
    const liCheck = elements.find(element => element.tagName.toLowerCase() === "li");
    if (liCheck != undefined && liCheck != currElement) {
        currElement.classList.remove("active");
        currElement = liCheck;
        currElement.classList.add("active");
        const headerText = currElement.querySelector('h3').textContent;

        if (!scrollActing) {
            gsap.to(".content-holder", { duration: 0, scrollTo: { y: document.getElementById(contentIDs[headerText]), positionInViewport: "center" }, ease: "none" });
        }

        const lastElement = currElement;
        setTimeout((prevElement) => {
            if (prevElement == currElement) {
                console.log("Remained")
                if (video_address.hasOwnProperty(headerText)) {
                    console.log("Creating video...")
                    createVideo(video_address[headerText])
                } else {
                    deleteVideo()
                }
            }
        }, 500, lastElement)
    }
}

sidebar.addEventListener("scroll", (event) => {
    updateCanvasOnScroll();
})

gsap.registerPlugin(ScrollTrigger);
gsap.registerPlugin(ScrollToPlugin);

const listParent = document.querySelector("#sidebar");
const listChilds = Array.from(listParent.children);

function checkNextElement() {
    const targets = document.querySelectorAll('.portfolio-content');
    const currElementIndx = listChilds.indexOf(currElement);
    const currElementPos = ScrollTrigger.positionInViewport(targets[currElementIndx - 1], "bottom").toFixed(2);
    if (currElementIndx > 1) {
        const prevElementPosBottom = ScrollTrigger.positionInViewport(targets[currElementIndx - 2], "bottom").toFixed(2);
        if (prevElementPosBottom > 0.6) {
            scrollToProject("previous");
        }
    }
    if (currElementPos < 0.3) {
        scrollToProject("next");
    }
}


function scrollToProject(option) {
    const currElementIndx = listChilds.indexOf(currElement);
    console.log(currElement);
    let nextElement = listChilds[currElementIndx];

    if (option == "previous") {
        nextElement = listChilds[currElementIndx - 2];
    }
    if (nextElement) {
        currElement.classList.remove("active");
        // Create a global variable set to true
        scrollActing = true;
        gsap.to(listParent, { duration: 0.7, scrollTo: { y: nextElement, positionInViewport: "center" }, ease: "power2", onComplete: () => scrollActing = false });
        currElement = nextElement;
    }

}


const contentHolder = document.querySelector(".content-holder");
contentHolder.addEventListener("scroll", () => checkNextElement());

var sec = document.querySelectorAll(".portfolio-content") // Will be replaced by ID
let sections = gsap.utils.toArray(sec.children);


sec.forEach((sec) => gsap.utils.toArray(sec.children).forEach((section) => {

    if (section.localName == "video") {
        console.log("Video!")
        let tl = gsap.timeline({
            scrollTrigger: {
                trigger: section,
                scrub: true,
                pin: true,
                scroller: ".content-holder",
                start: "top top",
                end: "+=100%",
            }
        })
        tl.to(section, { opacity: 1, duration: 0.5 }).to(section, { opacity: 0, duration: 0.5 }, 0.5)
    } else {
        gsap.from(section, {
            scrollTrigger: {
                trigger: section,
                scrub: true,
                pin: true,
                scroller: ".content-holder",
                start: "top top",
                end: "+=100%"
            },
            "--top-clip": '80%',
            opacity: 0,
            ease: "none",
        })
    }
}))

